export const items00 = [
  'Santrol brand',
  ' brand Motorized zone valve is used for controlling the opening or closing of cooling/hot water pipe of the HVACR system, to achieve the purpose of controlling room temperature. The zone valve is driven by a Unidirectional hysteresis synchronous motor, and the spring return. The valve is normally closed when it is not working. When it needs to work, the thermostat provides an opening signal to make the actuator connected with AC power to run. When the valve is opened, the chilled water or hot water enters the cooling/heating terminal unit like fan coil unit to cool/heat the room; When the room temperature reaches the setting value, the thermostat make the actuator power-off, and the return spring would close the valve, thereby intercepting the water flow into the terminal unit. By closing or opening the valve, the room temperature is always kept within the temperature range set by the thermostat.'
];
