            padding-bottom: 10px;
          }
          .chooseProd::after {
            content: "";
            display: block;
            position: relative;
            top: 10px;
            left: 10%;
            width: 80%;
            height: 2px;
            background: black;
            animation: bottomLine 1s;
          }
        }
        section:hover {
          cursor: pointer;
          animation: tdAnimation .3s;
        }
      }
      .m1250 {
        display: none;
      }
    }
    @media screen and (max-width: 1250px){
      .m1250 {
        margin: .6rem;
      }
    }
  }
}
.el-dropdown {
  vertical-align: top;
}
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
@keyframes tdAnimation {
  0% {
    position: relative;
    bottom: 0px;
  }
  50% {
    position: relative;
    bottom: 5px;
  }
  100% {
    position: relative;
    bottom: 0px;
  }
}
@keyframes bottomLine {
  0% {
    width: 0%;
  }
  100% {
    width: 80%;
  }
}
</style>
